<template>
	<view>
		<view class="row" style="width: 100%;height: 44px;background-color: #FFF;justify-content: space-around;align-items: center;">
			<!-- 评论状态选择 -->
			<view class="row" @click="showCommentClassList()" style="align-items: center;">{{currentCommentTxt}}
				<image
					:src="isShowCommentClass? '../../../static/mine/mine-uparrow@2x.png' : '../../../static/mine/mine-downarrow@2x.png'"
					mode="aspectFit" style="width: 15px;height: 15px;margin-left: 5px;">
				</image>
			</view>
			<!-- 回复状态选择 -->
			<view class="row" @click="showReplyClassList()" style="align-items: center;">{{currentReplyTxt}}
				<image
					:src=" isShowReplyClass? '../../../static/mine/mine-uparrow@2x.png' : '../../../static/mine/mine-downarrow@2x.png'"
					mode="aspectFit" style="width: 15px;height: 15px;margin-left: 5px;">
				</image>
			</view>
		</view>
		<view class="classView" v-if="isShowCommentClass">
			<view v-for="(item,index) in pinglunClassArr" :key="index">
				<view class="classItem" @click="commentItemClick(item,index)">{{item}}</view>
			</view>
		</view>
		<view class="classView" v-if="isShowReplyClass">
			<view v-for="(item,index) in replyClassArr" :key="index">
				<view class="classItem" @click="replyClassItemClick(item,index)">{{item}}</view>
			</view>
		</view>
		<view v-if="showResult" class="search">{{i18n.shop.search1}}{{number}}{{i18n.shop.search2}}</view>
		<block v-if="!isShowCommentClass && !isShowReplyClass">
			<!-- <view v-if="list.length == 0" class="nothing clomun">
				<image src="../../static/noorder@2x.png" mode="aspectFit" style="width: 100px;height:60px ;"></image>
				<view style="font-size:12px;height: 20px;line-height: 20px; color:#dadada;">{{i18n.shop.nocomment}}</view>
			</view>	 -->
			<!-- <view v-else> -->
				<scroll-view scroll-y="true" :style="'height:' + scrollHeight2 + 'px;'"
					style="width: 100%;background-color: #fff;" class="column" :refresher-threshold="45"
					:refresher-triggered="refresherTriggered" :refresher-enabled="true" @refresherrefresh="refresherrefresh"
					@scrolltolower="reloadmoreData">
					<pingjia :height="scrollHeight2" :typeString="1" :listArr="list"></pingjia>
				</scroll-view>
			<!-- </view> -->
		</block>
	</view>
</template>

<script>
	import pingjia from '@/components/pingjia/pingjia.vue'
	export default {
		components: {
			pingjia
		},
		data() {
			return {
				scrollHeight: 0,
				isShowCommentClass: false,
				isShowReplyClass: false,
				selectedCommentIndex: 0,
				selectedReplyIndex: 0,
				pinglunClassArr: [],
				replyClassArr: [],
				number: '',
				scrollHeight2: 0,
				page: 1,
				list: [],
				currentCommentTxt: '',
				currentReplyTxt: '',
				showResult: false,
				refresherTriggered: false,
			}
		},
		computed: {
			i18n() {
				return this.$t('index')
			},
		},
		onReady() {
			const res = uni.getSystemInfoSync();
			this.scrollHeight = res.windowHeight - 64 - res.statusBarHeight - 44
			this.scrollHeight2 = res.windowHeight - 20
		},
		onLoad() {
			uni.setNavigationBarTitle({
				title: this.i18n.shop.pinglun
			})
			this.currentCommentTxt = this.i18n.shop.pinglun1
			this.currentReplyTxt = this.i18n.shop.replystatus
			this.pinglunClassArr = [this.i18n.shop.all, this.i18n.shop.newpinglun, this.i18n.shop.picpinglun]
			this.replyClassArr = [this.i18n.shop.all,this.i18n.shop.replyed, this.i18n.shop.noreply]
			this.getListData()
			uni.$on('gotoDetail',(param) => {
				console.log(param)
				uni.navigateTo({
					url:'commentDetail?param=' + JSON.stringify(param) 
				})
			})
			var that = this
			uni.$on('refreshReply',(param) => {
				that.getListData()
			})	
				
		},
		onUnload() {
			uni.$off('gotoDetail')
			uni.$off('refreshReply')
		},
		methods: {
			getListData() {
				var that = this
				var param = {
					'review_status': String(that.selectedCommentIndex),
					'recoil_status': String(that.selectedReplyIndex),
					'p': String(that.page)
				}
				
				this.http.sendRequest('MerchantStoreOrderEvaluate.Index', param).then(function(data) {
					// console.log(data)
					if (data.code == 0) {
						that.number = data.info[1].count
						if (that.page == 1) {
							that.list = data.info[0]
						} else {
							var arr = data.info[0]
							for (let i = 0; i < arr.length; i++) {
								that.list.push(arr[i])
							}
						}
					}
				})
			},
			showCommentClassList() {
				this.isShowCommentClass = true
				this.isShowReplyClass = false
			},
			showReplyClassList() {
				this.isShowReplyClass = true
				this.isShowCommentClass = false
			},
			replyClassItemClick(item, index) {
				this.isShowReplyClass = false
				this.selectedReplyIndex = index
				this.currentReplyTxt = item
				this.page = 1
				this.showResult = true
				this.getListData()
			},
			commentItemClick(item, index) {
				this.isShowCommentClass = false
				this.selectedCommentIndex = index
				this.page = 1
				this.currentCommentTxt = item
				this.showResult = true
				this.getListData()
			},
			refresherrefresh() {
				this.refresherTriggered = true;
				this.page = 1
				this.getListData()
				setTimeout(() => {
					this.refresherTriggered = false;
				}, 1000);
			},
			reloadmoreData() {
				this.page++
				this.getListData()

			}

		}
	}
</script>

<style>
	page {
		width: 100%;
		height: 100%;
		background-color: #f5f5f5;
		overflow: hidden;
	}
	.nothing {
		margin-top:150px;
		width: 100%;
		height: 140px;
		display: flex;
		flex-direction: column;
		align-items: center;
	}
	.classView {
		background-color: rgba(0, 0, 0, 0.2);
		width: 100%;
		height: 100%;
		display: flex;
		flex-direction: column;
	}

	.classItem {
		width: 100%;
		height: 40px;
		line-height: 40px;
		padding-left: 15px;
		font-size: 14px;
		color: #181818;
		background-color: white;
		border-bottom: 1px solid #e6e6e6;
	}

	.search {
		width: 100%;
		height: 44px;
		background-color: #f5f5f5;
		color: #AAAAAA;
		padding-left: 25px;
		font-size: 14px;
		display: flex;
		flex-direction: row;
		align-items: center;
	}
</style>
